<template>
  <div class="hello">
    <div ref="mapbox" style="width:800px;height:500px;margin:0 auto"></div>
    <!-- 初始化echarts需要有个宽高的盒子 -->
  </div>
</template>

<script>
import echarts from 'echarts'
import 'echarts/map/js/china.js'
import jsonp from 'jsonp'
import axios from 'axios'
const option = {
            title: {
                text: '新增确诊/疑似'
            },
            tooltip: {},
            legend: {
                data:['新增确诊','疑似']
            },
            xAxis: {
                data: [],
                axisLabel: {  
                interval:0,  
                rotate:40  
                }        
            },
            yAxis: {},
            series: [{
                name: '新增确诊',
                type: 'line',
				smooth:true,
                data: []
            },{
                name: '疑似',
                type: 'line',
				smooth:true,
                data: []
            }],  tooltip: {
            // 鼠标放上去显示内容
                trigger: 'item'
            },
            toolbox: {
            // 下载
            show: true,
            orient: 'vertical',
            left: 'right',
            top: 'center',
            feature: {
            dataView: {readyOnly: false},
            restore: {},
            saveAsImage: {}
            }
        }
    };

export default {
  name: 'InC',
  mounted () {
    this.getData()
    this.mycharts = echarts.init(this.$refs.mapbox)
    // 初始化echarts
    this.mycharts.setOption(option)
  },
  methods: {
    getData () {
        axios.get('http://47.107.243.207/api/get_ncov_trend/currentConfirmedCount')
             .then((response)=>{
                console.log(response.data);
                option.xAxis.data=response.data.date;
                option.series[0].data=response.data.increaseComfirm;
                option.series[1].data=response.data.suspect;
                this.mycharts.setOption(option)
             });
    }
  }
}
</script>