    <img src="../assets/photo.jpg" class="photo">
    <div class="content">
      <div id="China">
      <strong class="hL">国内疫情</strong>
      <p class="time">数据更新时间{{lastUpdateTime}}</p>
      <div class="gaiLan">
        <div class="count">
          <div class="tab">
            <p>现有确诊</p>
            <strong>{{total.currentconfirm}}</strong>
            <p class="time">昨天{{today.storeConfirm}}</p>
          </div>
          <div class="tab">
            <p>无症状</p>
            <strong>{{total.noSymptom}}</strong>
            <p class="time">昨天{{today.noSymptom}}</p>
          </div>
          <div class="tab">
            <p>现有疑似</p>
            <strong>{{total.suspect}}</strong>
            <p class="time">昨天{{today.suspect}}</p>
          </div>
          <div class="tab">
            <p>现有重症</p>
            <strong>{{total.severe}}</strong>
            <p class="time">昨天{{today.severe}}</p>
          </div><div class="tab">
            <p>累计确诊</p>
            <strong>{{total.confirm}}</strong>
            <p class="time">昨天{{today.confirm}}</p>
          </div><div class="tab">
            <p>境外输入</p>
            <strong>{{total.input}}</strong>
            <p class="time">昨天{{today.input}}</p>
          </div><div class="tab">
            <p>累计治愈</p>
            <strong>{{total.heal}}</strong>
            <p class="time">昨天{{today.heal}}</p>
          </div><div class="tab">
            <p>累计死亡</p>
            <strong>{{total.dead}}</strong>
            <p class="time">昨天{{today.dead}}</p>
          </div>
        </div>
        <el-divider></el-divider>
        <div class="place">
	      <p class="pwrap">
	        <v-distpicker :hide-area="true" @province="onChangeProvince" @city="onChangeCity"></v-distpicker>
	      </p>
        <div id="table">
          <table id="table-1" style="text-align:center">
            <tr>
              <th>地市名称</th>
              <th>现存确诊</th>
              <th>累计确诊</th>
              <th>治愈</th>
              <th>死亡</th>
            </tr>
            <tr>
              <td>{{txt1}}</td>
              <td>{{provice.currentConfirmedCount}}</td>
              <td>{{provice.confirmedCount}}</td>
              <td>{{provice.curedCount}}</td>
              <td>{{provice.deadCount}}</td>
            </tr>
             <tr>
              <td>{{txt2}}</td>
              <td>{{city.currentConfirmedCount}}</td>
              <td>{{city.confirmedCount}}</td>
              <td>{{city.curedCount}}</td>
              <td>{{city.deadCount}}</td>
            </tr>
          </table>
        </div>
        </div>
      </div>
      <div class="map">
        <h3>全国疫情地图</h3>
        <el-tabs type="border-card" :stretch="true" label="全国疫情地图" v-model="activeName"> 
          <el-tab-pane label="现有确诊" name="1"><CMapN></CMapN></el-tab-pane>
          <el-tab-pane label="累计确诊" name="2"><CMap></CMap></el-tab-pane>
        </el-tabs>
      </div>
      <div class="map">
        <h3>全国疫情趋势图</h3>
        <el-tabs type="border-card" :stretch="true" label="全国疫情趋势图" v-model="active" tab-position="bottom"> 
          <el-tab-pane label="新增确诊/疑似趋势图" name="1"><InC></InC></el-tab-pane>
          <el-tab-pane label="累计确诊/疑似趋势图" name="2"><TC></TC></el-tab-pane>
          <el-tab-pane label="累计死亡/治愈趋势图" name="3"><CDC></CDC></el-tab-pane>
        </el-tabs>
      </div>
      </div>
      <div id="world">
      <strong class="hL">全球疫情</strong>
      <p class="time">数据更新时间{{lastUpdateTimeW}}</p>
      <div class="gaiLan">
        <div class="count">
          <div class="tab">
            <p>现有确诊</p>
            <strong>{{totalW.currentconfirm}}</strong>
            <p class="time">昨天{{todayW.storeConfirm}}</p>
          </div><div class="tab">
            <p>累计确诊</p>
            <strong>{{totalW.confirm}}</strong>
            <p class="time">昨天{{todayW.confirm}}</p>
          </div><div class="tab">
            <p>累计治愈</p>
            <strong>{{totalW.heal}}</strong>
            <p class="time">昨天{{todayW.heal}}</p>
          </div><div class="tab">
            <p>累计死亡</p>
            <strong>{{totalW.dead}}</strong>
            <p class="time">昨天{{todayW.dead}}</p>
          </div>
        </div>
      </div>
      <div class="map">
        <h3>全球疫情地图</h3>
        <el-tabs type="border-card" :stretch="true" label="全球疫情地图" v-model="activeNameW"> 
          <el-tab-pane label="现有确诊" name="1"><WMapN></WMapN></el-tab-pane>
          <el-tab-pane label="累计确诊" name="2"><WMap></WMap></el-tab-pane>
        </el-tabs>
      </div>
      <div class="map">
        <h3>全球疫情趋势图</h3>
        <el-tabs type="border-card" :stretch="true" label="全球疫情趋势图" v-model="activeW" tab-position="bottom"> 
          <el-tab-pane label="国内/国外新增确诊" name="1"><compare></compare></el-tab-pane>
          <el-tab-pane label="国外累计确诊/现有确诊" name="2"><confirm></confirm></el-tab-pane>
          <el-tab-pane label="国外治愈/死亡" name="3"><CDW></CDW></el-tab-pane>
        </el-tabs>
      </div>
      </div>
    </div>
    <div id="share">
        <el-dropdown @command="handleCommand">
          <el-button round>
            分享疫情数据<i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="WX">微信（打开微信扫描二维码）</el-dropdown-item>
            <el-dropdown-item command="QQ">QQ</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
    </div>
  </div>
</template>

<script>
import wx from 'weixin-js-sdk';
import CDW from '../components/cureDeadW.vue'
import confirm from '../components/confirm.vue'
import compare from '../components/compare.vue'
import CDC from '../components/cureDeadChina.vue'
import TC from '../components/totalChina.vue'
import InC from '../components/increaseChina.vue'
import CMapN from '../components/ChinaMapNow.vue'
import WMapN from '../components/worldMapNow.vue'
import CMap from '../components/ChinaMap.vue'
import WMap from '../components/worldMap.vue'
/*import echarts from 'echarts'
import 'echarts/map/js/china.js'
import jsonp from 'jsonp'*/
import axios from 'axios'
import VDistpicker from 'v-distpicker'
  export default {
    name: 'getAddress',
    components: { VDistpicker,
      CMap,
      CMapN,
      InC,
      TC,
      CDC,
      WMapN,
      WMap,
      compare,
      confirm,
      CDW
    },
    data() {
      return {
        city:{
          cityName:"呼和浩特",
          confirmedCount:0,
          curedCount:0,
          currentConfirmedCount:0,
          deadCount:0,
          suspectedCount:0
        },
        provice:{
          confirmedCount:0,
          curedCount:0,
          currentConfirmedCount:0,
          deadCount:0,
          suspectedCount:0
        },
        activeW:"1",
        activeNameW:"1",
        active:"1",
        activeName:"1",
        lastUpdateTime:'',
        lastUpdateTimeW:'',
        todayW:{
          confirm: 112364,
          dead: 8670,
          heal: 55912,
          storeConfirm: 47782
        },
        totalW:{
          confirm: 7166989,
          currentconfirm: 3264251,
          dead: 411565,
          heal: 3491173
        },
        total:{
          currentconfirm:127,
          confirm: 84614, 
          dead: 4645, 
          heal: 79842, 
          input: 1768, 
          noSymptom: 297, 
          severe: 1, 
          suspect: 2
        },
        today:{
          storeConfirm:"+"+5,
          confirm: '+'+11, 
          dead: '+'+0, 
          heal: '+'+11, 
          input: '+'+5, 
          noSymptom: "+"+3, 
          severe: '+'+0, 
          suspect: '+'+0
        },
        txt1:'省',
        txt2:'市',
        tableData: [{
            xq:0,
            lq:0,
            zy:0,
            sw:0
          }, {
            xq:0,
            lq:0,
            zy:0,
            sw:0
          }]
      }
    },
    methods: {
      onChangeProvince(a){
        console.log(a) 
        axios.get('http://47.107.243.207/api/get_city_data/'+a.value)
            .then((response)=>{
                console.log(response.data);
                this.provice=response.data;
            })
            .catch(function(error){
                console.log(error);
            });
        this.txt1 = a.value
      },    
      onChangeCity(a){
        console.log(a)  
        let name=(a.value).substring(0,2);
        console.log(name);
        axios.get('http://47.107.243.207/api/get_city_data/'+this.txt1+'/'+name)
            .then((response)=>{
                console.log(response.data);
                this.city=response.data;
            })
            .catch(function(error){
                console.log(error);
            });
        this.txt2 = a.value
      },
      handleCommand(command) {
        console.log(command);
        if(command=="QQ"){
          window.open('http://connect.qq.com/widget/shareqq/index.html?url=http://47.107.243.207&title=新冠疫情实时报告&summary=请用电脑访问');
        }
        else if(command=="WX"){
          var url = 'http://47.107.243.207',
          encodePath = encodeURIComponent(url),
          targetUrl = 'http://qr.liantu.com/api.php?text=' + encodePath;
          window.open(targetUrl,'weixin', 'height=320, width=320')
        }
      }
    },
    mounted(){
      axios.get('http://47.107.243.207/api/get_ncov_totalcount')
            .then((response)=>{
                console.log(response.data);
                this.lastUpdateTime=response.data.lastUpdateTime;
                this.total=response.data.total;
                this.today=response.data.today;
                if(this.today.storeConfirm>=0)  this.today.storeConfirm='+'+this.today.storeConfirm;
                if(this.today.confirm>=0)  this.today.confirm='+'+this.today.confirm;
                if(this.today.dead>=0)  this.today.dead='+'+this.today.dead;
                if(this.today.heal>=0)  this.today.heal='+'+this.today.heal;
                if(this.today.input>=0)  this.today.input='+'+this.today.input;
                if(this.today.noSymptom>=0)  this.today.noSymptom='+'+this.today.noSymptom;
                if(this.today.severe>=0)  this.today.severe='+'+this.today.severe;
                if(this.today.suspect>=0)  this.today.suspect='+'+this.today.suspect;
            })
            .catch(function(error){
                console.log(error);
            });
      axios.get('http://47.107.243.207/api/get_ncov_global_totalcount')
            .then((response)=>{
                console.log(response.data);
                this.lastUpdateTimeW=response.data.lastUpdateTime;
                this.totalW=response.data.total;
                this.todayW=response.data.today;
                if(this.todayW.storeConfirm>=0)  this.todayW.storeConfirm='+'+this.todayW.storeConfirm;
                if(this.todayW.confirm>=0)  this.todayW.confirm='+'+this.todayW.confirm;
                if(this.todayW.dead>=0)  this.todayW.dead='+'+this.todayW.dead;
                if(this.todayW.heal>=0)  this.todayW.heal='+'+this.todayW.heal;
            })
            .catch(function(error){
                console.log(error);
            });
    }
  }
</script>

