}
.head{
  color:#fff;
  position: fixed;
  left: 16%;
}
.home{
  background-color: #b3d2e6;
  width: 100%;
   top: 11%;
  
    bottom:0;
  
    position:fixed;
  
    overflow-y:scroll;
  
    overflow-x:hidden;
  right: 0;
}
